body {
  font-family: Roboto, sans-serif;
}

#main-title{
  font-family: 'Risque', cursive;
}

/* -----Search----- */
#search-div{
  border-radius: 25px;
  border: 1px solid #ced4da;
  /* width: 35em; */
  margin: auto;
  padding-bottom: 0px;
}

#main-search{
  border-radius: 25px 0px 0px 25px;
  border: none;
  padding-top: 9px;
  padding-left: 15px;
  /* margin-left: .5px; */
  margin-bottom: 0px;
}
#searched{
  border-radius: 25px 0px 0px 25px;
  border: none;
  padding-top: 9px;
  padding-left: 15px;
  margin-bottom: 0px;
}


#search-button{
  border-radius: 0px 25px 25px 0px;
  font-size: 20px;
  border: none;
  color: gray;
  cursor: pointer;
  padding-top: 6px;
  padding-left: 6px;
  margin-bottom: 0px;
  margin-right: 0px;
}


/* -----/Search----- */

header {
  padding: 20px 0;
}

header .row,
footer .row {
  display: flex;
  align-items: center;
}


.jumbotron {
  display: flex;
  align-items: center;
  text-align: center;
  background-size: cover;
  background-color: white;
  color: #ffffff;
  height: 400px;
}

.jumbotron h2 {
  font-size: 60px;
  font-weight: 700;
  text-align: center;
  color: #fff;
}

@media (max-width: 35em) {
  .jumbotron h2 {
      font-size: 55px;
  }
}

.jumbotron h3 {
  margin: 0 0 20px;
  color: #fff;
}
