footer {
  font-size: 12px;
  padding: 20px 0;
}

/* -----------------  Experience Page Cards   ------------------ */

@media (min-width: 1em) {
  .card-columns {
      -webkit-column-count: 2;
      -moz-column-count: 2;
      column-count: 2;
  }
  /*  title */
  #main-title{
    font-family: 'Risque', cursive;
    font-size: 2em;
  }
  #search-div{
    width: 100%;
  }
}
@media (min-width: 36em) {
  .card-columns {
      -webkit-column-count: 3;
      -moz-column-count: 3;
      column-count: 3;
  }
} 
@media (min-width: 48em) {
  /*  title */
  #main-title{
    font-family: 'Risque', cursive;
    font-size: 8em;
  }
  #search-div{
    width: 35em;
  }
  #main-search{
    width: 100em
  }
} 

/* s */
.card-header{
  background-color: rgba(0,0,0,.045);
  font-size: 11px;
  padding: 0;
}
